.lowin {
	/* variables */
	--color-primary: #4487b3;
	--color-grey: rgba(68, 160, 179, .06);
	--color-dark: rgba(68, 160, 179, .5);
	--color-semidark: rgba(68, 160, 179, .5);

	text-align: center;
	margin: 60px 0 0 0;
	font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
	font-size: 16px;
}

.lowin .lowin-wrapper {
	-webkit-perspective: 1000px;
	        perspective: 1000px;
	position: relative;
	height: 100%;
	width: 360px;
	margin: 0 auto;
}

.lowin.lowin-blue {

	--color-primary:	#0081C6;;
	--color-grey: rgba(0, 129, 198, .05);
	--color-dark: rgba(0, 129, 198, .7);
	--color-semidark: rgba(0, 129, 198, .45);
}

.lowin a {
	color: var(--color-primary);
	-webkit-text-decoration: #0081C6;
	        text-decoration: #0081C6;
	border-bottom: 1px dashed var(--color-semidark);
	margin-top: -3px;
	padding-bottom: 2px;
}

.lowin * {
	box-sizing: border-box;
}

.lowin .lowin-brand {
	overflow: hidden;
	width: 100px;
	height: 100px;
	margin: 0 auto -50px auto;
	border-radius: 50%;
	box-shadow: 0 4px 40px rgba(0, 0, 0, .07);
	padding: 10px;
	background-color: #fff;
	z-index: 1;
	position: relative;
}

.lowin .lowin-brand img {
	width: 100%;
}

.lowin .lowin-box {
	width: 100%;
	position: absolute;
	left: 0;
}

.lowin .lowin-box-inner {
	background-color: #fff;
	box-shadow: 0 7px 25px rgba(0, 0, 0, .08);
	padding: 60px 25px 25px 25px;
	text-align: left;
	border-radius: 5px;
}

.lowin .lowin-box p {
	color: var(--color-semidark);
	font-weight: 700;
	margin-bottom: 20px;
	text-align: center;
}

.lowin .lowin-box .lowin-group {
	margin-bottom: 30px;
}

.lowin .lowin-box label {
	margin-bottom: 10px;
	display: inline-block;
	width: 100%;
	color: var(--color-semidark);
	font-weight: 700;
}

.lowin .lowin-box .lowin-input {
	background-color: var(--color-grey);
	color: var(--color-dark);
	border: #fff;
	border-radius: 3px;
	padding: 15px 20px;
	width: 100%;
}
.lowin .lowin-box .lowin-group .radio1 {
	color: var(--color-semidark);
	font-weight: 700;
	margin-bottom: 20px;
}
.lowin .lowin-box .lowin-btn {
	display: inline-block;
	width: 100%;
	border: none;
	color: #fff;
	padding: 15px;
	border-radius: 3px;
	background-color: var(--color-primary);
	box-shadow: 0 2px 7px var(--color-semidark);
	font-weight: 700;
	cursor: pointer;
}

.lowin .text-foot {
	text-align: center;
	padding: 10px;
	font-weight: 700;
	margin-top: 20px;
	color: var(--color-semidark);
}
*{
	margin:0;
	padding:0;
	}
	
	/*
	* 设置ul
	*/
	.nav{
	/*去除项目符号*/
	list-style: none;
	/*为ul设置一个背景颜色*/
	background-color: rgb(168, 192, 238);
	/*设置一个宽度*/
	/*
	* 在IE6中，如果为元素指定了一个宽度，则会默认开启hasLayout
	*/
	width: 1000px;
	/*设置元素居中*/
	margin: 50px auto;
	/*解决高度塌陷*/
	overflow: hidden;
	}
	
	/**
	* 设置li
	*/
	.nav li{
	/*设置li向左浮动*/
	float: left;
	width: 25%;
	}
	
	.nav a{
	/*将a转换为块元素*/
	display: block;
	/*为a指定一个宽度*/
	width: 100%;
	/*设置文字居中*/
	text-align: center;
	/*设置一个上下内边距*/
	padding: 5px 0;
	/*去除下划线*/
	text-decoration: none;
	/*设置字体颜色*/
	color: white;
	/*设置加粗*/
	font-weight: bold;
	}
	
	/*
	* 设置a的鼠标移入的效果
	*/
	.nav a:hover{
	background-color:rgb(3, 64, 97);
	}
